@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;400&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 100;
}

img {
  width: 100%;
  display: block;
}
.container{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(211, 211, 211, 0.3);
}

.card {
  width: 350px;
  height: 500px;
  background-color: rgb(248, 255, 246);
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.header {
  width: 100%;
  height: calc(100% / 6 * 4);
  overflow: hidden;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.header .title {
  font-weight: 300;
  padding-bottom: 0.5rem;
}

.header img {
  padding: 0 2rem;
}

.middle {
  width: 100%;
  height: calc(100% / 6 * 1);
  background-color: rgb(225, 224, 167);
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
}

.footer {
  width: 100%;
  height: calc(100% / 6 * 1);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 2rem;
  text-align: center;
}
